<template>
  <Transition name="fadeDown">
    <div class="physton-prompt-hotkey" v-if="isOpen" @click="onCloseClick">
      <div class="hotkey-main" @click.stop>
        <div class="hotkey-popup-close" @click="onCloseClick">
          <icon-svg name="close" />
        </div>
        <div class="hotkey-body" @click.stop>
          <icon-svg v-if="loading" style="width: 100%; height: 100%; box-sizing: border-box" name="loading" />
          <div v-if="!loading" class="hotkey-list">
            <div style="font-size: 20px; color: #fff">Tag</div>
            <div style="margin-bottom: 15px; margin-top: 10px; color: #fff">
              Tag
            </div>
            <div style="display: flex; align-items: center; margin-bottom: 10px">
              <div style="color: #fff; margin-right: 10px"></div>
              <input disabled type="text" v-model="cnValue" />
            </div>
            <div style="display: flex; align-items: center">
              <div style="color: #fff; margin-right: 10px"></div>
              <input disabled type="text" v-model="enValue" />
            </div>
          </div>
          <div v-if="!loading" class="setting-btns">
            <div class="hotkey-save hover-scale-120" @click="onSaveClick">
              
            </div>
            <div class="hotkey-close hover-scale-120" @click="onCloseClick">
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
import IconSvg from "@/components/iconSvg.vue";
import popmessage from "@/utils/message";

export default {
  name: "DeleteKey",
  components: { IconSvg },
  data() {
    return {
      isOpen: false,
      enValue: "",
      cnValue: "",
      firstKeyName: "",
      secondKeyName: "",
      loading: false,
      lauCode: "zh_CN",
    };
  },
  methods: {
    open(key, group, cn, en, la) {
      this.loading = false;
      this.enValue = en;
      this.cnValue = cn;
      this.firstKeyName = key;
      this.secondKeyName = group;
      this.lauCode = la;
      this.isOpen = true;
      //   console.log(la);
    },
    close() {
      this.$emit("addSuccess", "yes");
      this.isOpen = false;
    },
    onCloseClick() {
      this.close();
    },
    onSaveClick() {
      if (this.cnValue.length <= 0 || this.enValue.length <= 0) {
        popmessage({
          type: "warn",
          str: "Tag Please fill in the complete Tag",
        });
        return;
      }
      this.loading = true;
      this.gradioAPI
        .deleteGroupTags(
          this.lauCode,
          this.cnValue,
          this.enValue,
          this.firstKeyName,
          this.secondKeyName
        )
        .then((res) => {
          // console.log(res.data.data)
          popmessage({ type: "success", str: " Add Success" });
          this.$nextTick(function () {
            this.loading = false;
            this.close();
          });
        })
        .catch((err) => {
          popmessage({ type: "warn", str: " Add Fail" });
          this.loading = false;
        });
    },
  },
};
</script>
