<template>
  <Transition name="fadeDown">
    <div class="physton-prompt-hotkey" v-if="isOpen">
      <div class="hotkey-main" @click.stop>
        <div class="hotkey-popup-close" @click="onCloseClick">
          <icon-svg name="close" />
        </div>
        <div class="hotkey-body" @click.stop>
          <icon-svg v-if="loading" style="width: 100%; height: 100%; box-sizing: border-box" name="loading" />
          <div v-if="!loading" class="hotkey-list">
            <div style="font-size: 20px; color: #fff;margin-bottom: 15px;width: 150px;">LLM</div>

            <div style="display: flex; align-items: center; margin-bottom: 10px">
              <div style="color: #fff; margin-right: 10px;width: 150px;"></div>
              <input type="text" v-model="jsonData.llmName" />
            </div>
            <div style="display: flex; align-items: center">
              <div style="color: #fff; margin-right: 10px;width: 150px;">gpu0~,-1gpu</div>
              <input type="text" v-model="jsonData.n_gpu_layers" />
            </div>
            <div style="display: flex; align-items: center">
              <div style="color: #fff; margin-right: 10px;width: 150px;">temperature</div>
              <input type="text" v-model="jsonData.temperature" />
            </div>
            <div style="display: flex; align-items: center">
              <div style="color: #fff; margin-right: 10px;width: 150px;">preset</div>
              <textarea rows="6" v-model="jsonData.preset" style="width: 300px;"></textarea>
            </div>
          </div>
          <div v-if="!loading" class="setting-btns">
            <div class="hotkey-save hover-scale-120" @click="onSaveClick">
              
            </div>
            <div class="hotkey-close hover-scale-120" @click="onCloseClick">
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
import IconSvg from "@/components/iconSvg.vue";
import popmessage from "@/utils/message";

export default {
  name: "EditLLMSetting",
  components: { IconSvg },
  data() {
    return {
      isOpen: false,
      loading: false,
      jsonData: {
        "llmName": "",
        "n_gpu_layers": -1,
        "preset":
          "",
        "temperature": 1.2
      }
    };
  },
  methods: {
    open() {
      this.loading = true;
      this.jsonData = {
        "llmName": "",
        "n_gpu_layers": -1,
        "preset":
          "",
        "temperature": 1.2
      }
      this.isOpen = true;
      this.gradioAPI
        .getLLMSettingConfig()
        .then((res) => {
          // console.log(res)
          this.jsonData = res.data
          this.$nextTick(function () {
            this.loading = false;
          });
        })
        .catch((err) => {
          popmessage({ type: "warn", str: "LLM" });
          this.loading = false;
          this.close()
        });
      //   console.log(la);
    },
    close() {
      this.isOpen = false;
    },
    onCloseClick() {
      this.close();
    },
    onSaveClick() {
      this.loading = true;
      this.gradioAPI
        .setLLMSettingConfig(this.jsonData)
        .then((res) => {
          // console.log(res.data.data)
          popmessage({ type: "success", str: " Add Success" });
          this.$nextTick(function () {
            this.loading = false;
            this.close();
          });
        })
        .catch((err) => {
          popmessage({ type: "warn", str: " Add Fail" });
          this.loading = false;
        });
    },
  },
};
</script>
