<template>
  <Transition name="fadeDown">
    <div class="physton-prompt-hotkey" v-if="isOpen" @click="onCloseClick">
      <div class="hotkey-main" @click.stop>
        <div class="hotkey-popup-close" @click="onCloseClick">
          <icon-svg name="close" />
        </div>
        <div class="hotkey-body" @click.stop>
          <icon-svg v-if="loading" style="width: 100%; height: 100%; box-sizing: border-box" name="loading" />
          <div v-if="!loading" class="hotkey-list">
            <div style="font-size: 20px; color: #fff">
              {{
                modelEs == 0
                  ? "Tag"
                  : modelEs == 1
                    ? "Tag"
                    : modelEs == 2
                      ? "Tag"
                      : "Tag"
              }}
            </div>
            <div style="margin-bottom: 15px; margin-top: 10px; color: #fff">
              {{
                modelEs == 0
                  ? ""
                  : modelEs == 1
                    ? ""
                    : modelEs == 2
                      ? ""
                      : ""
              }}
            </div>
            <div style="display: flex; align-items: center; margin-bottom: 10px">
              <div style="color: #fff; margin-right: 10px"></div>
              <input :disabled="modelEs == 1 || modelEs == 4 || modelEs == 3" type="text" v-model="firstKeyName" />
            </div>
            <div v-if="modelEs != 2" style="display: flex; align-items: center; margin-bottom: 10px">
              <div style="color: #fff; margin-right: 10px"></div>
              <input type="text" v-model="secondKeyName" />
            </div>
          </div>
          <div v-if="!loading" class="setting-btns">
            <div class="hotkey-save hover-scale-120" @click="onSaveClick">
              {{
                modelEs == 0
                  ? ""
                  : modelEs == 1
                    ? ""
                    : modelEs == 2
                      ? ""
                      : ""
              }}
            </div>
            <div class="hotkey-close hover-scale-120" @click="onCloseClick">
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
import IconSvg from "@/components/iconSvg.vue";
import popmessage from "@/utils/message";

export default {
  name: "AddNodeGroup",
  components: { IconSvg },
  data() {
    return {
      isOpen: false,
      enValue: "",
      cnValue: "",
      firstKeyName: "",
      secondKeyName: "",
      tempFirst: "",
      tempSecond: "",
      loading: false,
      lauCode: "zh_CN",
      modelEs: 0,
    };
  },
  methods: {
    open(key, group, la, model) {
      this.loading = false;
      this.refreshColor = true;
      this.enValue = "";
      this.cnValue = "";
      this.firstKeyName = "";
      this.secondKeyName = "";
      this.lauCode = la;
      this.modelEs = 0;
      if (model == 1) {
        this.modelEs = model;
        this.firstKeyName = key;
      }
      if (model > 1) {
        this.modelEs = model;
        this.firstKeyName = key;
        this.secondKeyName = group;
        this.tempFirst = key;
        this.tempSecond = group;
      }
      this.isOpen = true;
      //   console.log(la);
    },
    close() {
      this.$emit("addSuccess", { model: 1 });
      this.isOpen = false;
    },
    onCloseClick() {
      this.close();
    },
    onSaveClick() {
      if (this.modelEs <= 1) {
        if (this.firstKeyName.length <= 0 || this.secondKeyName.length <= 0) {
          popmessage({
            type: "warn",
            str: " Please fill in the complete Info",
          });
          return;
        }
      } else {
        if (this.modelEs == 2) {
          if (this.firstKeyName.length <= 0) {
            popmessage({
              type: "warn",
              str: " Please fill in the complete Info",
            });
            return;
          }
        } else if (this.modelEs == 3) {
          if (this.secondKeyName.length <= 0) {
            popmessage({
              type: "warn",
              str: " Please fill in the complete Info",
            });
            return;
          }
        }
      }
      this.loading = true;
      const strColor = "";
      if (this.modelEs == 0) {
        this.gradioAPI
          .addNewNodeGroupTags(
            this.lauCode,
            this.firstKeyName,
            this.secondKeyName,
            strColor
          )
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Add Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Add Fail" });
            this.loading = false;
          });
      } else if (this.modelEs == 1) {
        this.gradioAPI
          .addNewGroupTags(
            this.lauCode,
            this.firstKeyName,
            this.secondKeyName,
            strColor
          )
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Add Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Add Fail" });
            this.loading = false;
          });
      } else if (this.modelEs == 2) {
        this.gradioAPI
          .editNodeGroupTags(this.lauCode, this.tempFirst, this.firstKeyName)
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Edit Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Edit Fail" });
            this.loading = false;
          });
      } else if (this.modelEs == 3) {
        this.gradioAPI
          .editChildGroupTags(
            this.lauCode,
            this.firstKeyName,
            this.tempSecond,
            this.secondKeyName
          )
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Edit Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Edit Fail" });
            this.loading = false;
          });
      }
    },
  },
};
</script>
