<template>
  <Transition name="fadeDown">
    <div class="physton-chatgpt-prompt" v-if="isOpen">
      <div class="chatgpt-main" @click.stop>
        <div class="chatgpt-close" @click="close">
          <icon-svg name="close" />
        </div>
        <div class="chatgpt-body" @click.stop>
          <div style="display: flex;align-items: center;">
            <div class="line-content">
              <div class="llm-btn" @click="onCheckOrInstallLLM">
                /-LLM
              </div>
            </div>
            <div class="line-content" style="margin-left: 10px;">
              <div class="llm-btn" @click="openLLMSetting">
                LLM
              </div>
            </div>
            <div class="line-content" style="margin-left: 10px;">
              <div class="llm-btn" @click="testLLM">
                LLM
              </div>
            </div>
          </div>
          <div style="">
            <div>
              <div
                style="display: flex;align-items: center;justify-content: flex-start;margin-top: 10px;margin-bottom: 10px;">
                <p></p>
                <div class="llm-btn" @click="imageLLMReps" style="margin-left: 10px;">
                  
                </div>
              </div>
              <textarea v-model="imageDesc" class="llm-content" placeholder="" rows="10"
                style="width: 100%;"></textarea>
            </div>
            <div>
              <div
                style="display: flex;align-items: center;justify-content: flex-start;margin-top: 10px;margin-bottom: 10px;">
                <p></p>
                <div class="llm-btn" @click="sendToGreat" style="margin-left: 10px;">
                  
                </div>
                <div class="llm-btn" @click="sendToNeg" style="margin-left: 10px;">
                  
                </div>
                <div class="llm-btn" @click="copyTP" style="margin-left: 10px;">
                  
                </div>
              </div>
              <div style="position: relative;display: flex;align-items: center;justify-content: center;">
                <icon-svg v-if="loading" style="
                        position: absolute;
                        width: 50px;
                        height: 50px;
                        z-index: 2;
                        box-sizing: border-box;
                        " name="loading" />
                <textarea v-model="promptResult" class="llm-content" placeholder="" readonly rows="10"
                  style="width: 100%;"></textarea>
              </div>
            </div>
          </div>

        </div>
      </div>
      <LlmPackagesState ref="llmPackagesState" v-model:language-code="languageCode" :languages="languages" />
      <EditLLMSetting ref="editLLMSetting" />
    </div>
  </Transition>
</template>
<script>
import LanguageMixin from "@/mixins/languageMixin";
import IconSvg from "@/components/iconSvg.vue";
import common from "@/utils/common";
import LlmPackagesState from "@/components/llmPackagesState.vue";
import EditLLMSetting from "@/components/editLLMSetting.vue";
import popmessage from "@/utils/message";

export default {
  name: "LlmPrompt",
  components: { IconSvg, LlmPackagesState, EditLLMSetting },
  mixins: [LanguageMixin],
  props: {},
  data() {
    return {
      isOpen: false,
      imageDesc: "",
      promptResult: "",
      loading: false,
    };
  },
  emits: ["use"],
  computed: {},
  mounted() { },
  methods: {
    open() {
      this.imageDesc = ""
      this.promptResult = ""
      this.isOpen = true;
    },
    close() {
      this.isOpen = false;
    },
    onCheckOrInstallLLM() {
      this.$refs.llmPackagesState.open()
    },
    openLLMSetting() {
      this.$refs.editLLMSetting.open()
    },
    testLLM() {
      this.gradioAPI
        .testLLMTran()
        .then((res) => {
          // console.log(res)
          if (res.status != 200) {
            popmessage({ type: "warn", str: res.data.info });
          } else {
            popmessage({ type: "success", str: res.data.info });
          }

        })
        .catch((err) => {
          popmessage({ type: "warn", str: err });
        });
    },
    imageLLMReps() {
      this.loading = true
      this.promptResult = ""
      this.gradioAPI
        .imageLLMReps(this.imageDesc)
        .then((res) => {
          // console.log(res)
          if (res.status != 200) {
            popmessage({ type: "warn", str: res.data.info });
          } else {
            // popmessage({ type: "success", str: res.data.info });
            this.promptResult = res.data.info
          }
          this.loading = false

        })
        .catch((err) => {
          popmessage({ type: "warn", str: err });
          this.loading = false
        });

    },
    sendToGreat() {
      const evt = new CustomEvent("weilinPromptInnerSendToGreat", { "detail": { "text": this.promptResult } });
      document.dispatchEvent(evt);
    },
    sendToNeg() {
      const evt = new CustomEvent("weilinPromptInnerSendToNeg", { "detail": { "text": this.promptResult } });
      document.dispatchEvent(evt);
    },
    copyTP() {
      navigator.clipboard.writeText(this.promptResult).then(
        (res) => {
          popmessage({ type: "success", str: " Copy Success" });
        },
        (err) => {
          popmessage({ type: "warn", str: " Copy Fail" });
        }
      );
    },
  },
};
</script>