<template>
  <Transition name="fadeDown">
    <div class="physton-prompt-hotkey" v-if="isOpen" @click="onCloseClick">
      <div class="hotkey-main" @click.stop>
        <div class="hotkey-popup-close" @click="onCloseClick">
          <icon-svg name="close" />
        </div>
        <div class="hotkey-body" @click.stop>
          <icon-svg v-if="loading" style="width: 100%; height: 100%; box-sizing: border-box" name="loading" />
          <div v-if="!loading" class="hotkey-list">
            <div style="font-size: 20px; color: #fff">Tag</div>
            <div style="margin-bottom: 15px; margin-top: 10px; color: #fff">
              
            </div>
            <div style="display: flex; align-items: center; margin-bottom: 10px">
              <div style="color: #fff; margin-right: 10px"></div>
              <input disabled type="text" v-model="firstKeyName" />
            </div>
            <div v-if="modelEs == 2" style="display: flex; align-items: center">
              <div style="color: #fff; margin-right: 10px"></div>
              <input disabled type="text" v-model="secondKeyName" />
            </div>
          </div>
          <div v-if="!loading" class="setting-btns">
            <div class="hotkey-save hover-scale-120" @click="onSaveClick">
              
            </div>
            <div class="hotkey-close hover-scale-120" @click="onCloseClick">
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
import IconSvg from "@/components/iconSvg.vue";
import popmessage from "@/utils/message";

export default {
  name: "DeleteNodeGroup",
  components: { IconSvg },
  data() {
    return {
      isOpen: false,
      firstKeyName: "",
      secondKeyName: "",
      loading: false,
      lauCode: "zh_CN",
      modelEs: 0,
    };
  },
  methods: {
    open(key, group, la, model) {
      this.loading = false;
      this.firstKeyName = key;
      this.secondKeyName = group;
      this.lauCode = la;
      this.isOpen = true;
      this.modelEs = model;
      //   console.log(la);
    },
    close() {
      this.$emit("addSuccess", "yes");
      this.isOpen = false;
    },
    onCloseClick() {
      this.close();
    },
    onSaveClick() {
      this.loading = true;
      if (this.modelEs == 1) {
        this.gradioAPI
          .deleteNodeGroupTags(this.lauCode, this.firstKeyName)
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Add Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Add Fail" });
            this.loading = false;
          });
      } else if (this.modelEs == 2) {
        this.gradioAPI
          .deleteChildGroupTags(
            this.lauCode,
            this.firstKeyName,
            this.secondKeyName
          )
          .then((res) => {
            // console.log(res.data.data)
            popmessage({ type: "success", str: " Add Success" });
            this.$nextTick(function () {
              this.loading = false;
              this.close();
            });
          })
          .catch((err) => {
            popmessage({ type: "warn", str: " Add Fail" });
            this.loading = false;
          });
      }
    },
  },
};
</script>
