<template>
    <Transition name="fadeDown">
        <div class="physton-packages-state" v-if="isOpen" @click="close">
            <div class="state-main" @click.stop>
                <div class="state-close" @click="close">
                    <icon-svg name="close" />
                </div>
                <div class="state-body" @click.stop>
                    <icon-svg v-if="loading" style="
                        width: 400px;
                        height: 400px;
                        box-sizing: border-box;
                        " name="loading" />
                    <div v-else>
                        <div class="state-body-name">{{ globals.shortName }}</div>
                        <div class="state-body-desc">
                            LLM
                        </div>
                        <div class="package-list">
                            <div v-for="(item) in packagesState" :key="item.name" class="package-item">
                                <div class="package-name">{{ item.name }}</div>
                                <div :class="['package-state', item.state ? 'installed' : 'not_install']">{{
                                    getLang(item.state ? 'installed' : 'not_install') }}</div>
                                <div class="package-command">{{ getCommand(item) }}</div>
                            </div>
                        </div>
                        <div class="btn-packages-install hover-scale-120" @click="onInstallClick">
                            <icon-svg v-if="loading" name="loading" />
                            <template v-else>{{ getLang('install') }}</template>
                        </div>
                        <div class="install-result" v-if="showResult">
                            {{ getLang('packages_installing') }}
                            <div class="result-content" v-if="result" ref="result">{{ result }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </Transition>
</template>
<script>
import LanguageMixin from "@/mixins/languageMixin";
import IconSvg from "@/components/iconSvg.vue";
import popmessage from "@/utils/message";

export default {
    name: 'LlmPackagesState',
    components: { IconSvg },
    mixins: [LanguageMixin],
    data() {
        return {
            packagesState: [],
            python: './python',
            todayNotShow: false,
            isOpen: false,
            loading: false,
            showResult: false,
            result: '1112323\nsfsdfsdf\n2222\n3333\n1112323\nsfsdfsdf\n2222\n3333\n1112323\nsfsdfsdf\n2222\n3333',
        }
    },
    emits: [],
    computed: {},
    mounted() {
    },
    methods: {
        isInstalled(packageName) {
            let installed = false
            for (let item of this.packagesState) {
                if (item.name === packageName) {
                    return item.state
                }
            }
            return false
        },
        isAllInstalled() {
            for (let item of this.packagesState) {
                if (!item.state) {
                    return false
                }
            }
            return true
        },
        getCommand(item) {
            return `${this.python} -m pip install ${item.package}`
        },
        onInstallClick() {
            if (this.loading) return
            this.loading = true
            this.showResult = true
            this.result = ''
            let packages = []
            this.packagesState.forEach((item) => {
                if (!item.state) packages.push(item)
            })
            const complete = (state = false) => {
                this.loading = false
                if (state) {
                    this.result += '\ncomplete!'
                    setTimeout(this.close, 2000)
                }
                this.scrollToBottom()
            }
            const install = () => {
                let item = packages.shift()
                if (!item) return complete(true)
                this.gradioAPI.installLLMPackage(item.name, item.package).then(res => {
                    this.result += `${res.message}\n`
                    if (res.state) {
                        this.scrollToBottom()
                        install()
                    } else {
                        complete(false)
                    }
                }).catch(err => {
                    console.log(err)
                    this.result += `${item.name} install failed\n${err.message}\n`
                    complete(false)
                })
            }
            install()
        },
        scrollToBottom() {
            this.$nextTick(() => {
                this.$refs.result.scrollTop = this.$refs.result.scrollHeight
            })
        },
        open() {
            this.result = ''
            this.loading = true
            this.showResult = false
            this.isOpen = true

            this.gradioAPI
                .getLLMInstallConfig()
                .then((res) => {
                    // console.log(res)
                    this.packagesState = res.packages_state
                    this.python = res.python
                    this.loading = false
                })
                .catch((err) => {
                    popmessage({ type: "warn", str: "LLM" });
                    this.loading = false
                    this.close()
                });
        },
        close() {
            this.isOpen = false
        },
    },
}
</script>